.wrapper-container{
    border: 1px solid #ccc;
    border-radius: 10px;
    width: 100%;
    justify-content: center;
    margin: 0;
    background-color: #fff;
    color: #000;
    display: flex;
    flex-wrap: wrap;
}

.padder-five{
    margin: 5px 5px;
}

.padder-ten{
    margin: 10px 10px;
}

i.fa-user-circle{
    font-size: 60px;
    color: #4017ec;
}

.container-label{
    font-weight: bold;
}