body {
  width: 1366px;
  margin: 0 auto;
  overflow: hidden;
}
.header-section{
  background-color: #3219e4;
  padding: 5px 10px;
  color: #ffffff;
}

.container {
  border: 1px solid #ccc;
  height: 480px;
  border-radius: 10px;
  padding: 10px 10px;
  margin: 1% 4%;
  min-height: 480px;
  overflow-y: auto;
  background-color: #fff;
  box-shadow: 0 0 8px 2px #888;
}

.player-count{
  text-align: center;
  margin: 1% 4%;
}

.items-container{
  cursor: pointer;
  padding-bottom: 10px;
}

.player-wrapper{
  pointer-events: none;
  cursor: pointer;
}

.header-row{ 
  font-weight: 700;
  width: 100%;
  margin: 0;
}

.stop-propagation{
  pointer-events: none;
  cursor: not-allowed;
}

.thankyou-msg{
  justify-content: center;
  margin: 5px 0;
}

.App ::-webkit-scrollbar {
width: 5px;
height: 460px;
}

.App ::-webkit-scrollbar-track {
background: #f1f1f1; 
border-radius: 10px;
margin: 10px 0px;
}

.App ::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 10px;
margin: 10px 0;
}

.App ::-webkit-scrollbar-thumb:hover {
background: #555; 
}

@media only screen and (max-width: 787px) {
  body{
    width: 100%;
  }
}